import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';

import useModalControl from "../hooks/useModalControl";

import  ChatHeader  from '../components/header/ChatHeader';
import { ProfileSm } from '../components/common/Profile';
import { FileUploadSm } from '../components/common/FileUpload';
import { Modal } from './../components/common/Modal';
import SaleItem from '../assets/img/SaleItem.png';

export default function ChatRoom() {
  
  const chatHistory = [
    {
      Msg: '     .   ?     .    .     .',
      creatAt: '12:39',
      receive: true,
    },
    {
      Msg: '   ?',
      creatAt: '12:41',
      receive: true,
    },
    {
      Msg: '  ',
      creatAt: '12:50',
    },
    {
      Img: SaleItem,
      creatAt: '12:51',
    },
  ];

  const {openModal, ModalComponent} = useModalControl();
  const navigate = useNavigate()

  const handleChatRoomOut = () =>{
    navigate(-1)
  }

  return (
    <>
      <ChatHeader name={''} isButton={true} handleFunc={openModal} />
      <ChatRoomPageStyle>
        {chatHistory.map((item, index) =>
          item.receive ? (
            <ChatContainerStyle key={index}>
              <ProfileSm />
              <p>{item.Msg}</p>
              <span className="time">{item.creatAt}</span>
            </ChatContainerStyle>
          ) : (
            <MyChatContainerStyle key={index}>
              <span className="time">{item.creatAt}</span>
              {!!item.Img || <p>{item.Msg}</p>}
              <img src={item.Img} alt="" />
            </MyChatContainerStyle>
          )
        )}
      </ChatRoomPageStyle>
      <SendStyle>
        <div className="upload">
          <FileUploadSm />
        </div>
        <InputStyle type={'text'} placeholder=" ..."></InputStyle>
        <button id="send"></button>
      </SendStyle>
      <ModalComponent>
        <Modal contents={[' ']} handleFunc={handleChatRoomOut} />
      </ModalComponent>
    </>
  );
}

const ChatRoomPageStyle = styled.div`

  height: var(--screen-nav-height);
  padding-top: 24px;

`;

const ChatContainerStyle = styled.div`
  display: flex;
  margin: 0 16px 9px;

  p {
    display: inline-block;
    width: 240px;
    margin-left: 16px;
    padding: 10px;
    border: 1px solid #c4c4c4;
    border-radius: 0 10px 10px;
    font-size: var(--font--size-md);
    line-height: 19px;
    color: var(--text-color-1);
  }

  .time {
    display: flex;
    align-items: flex-end;
    margin: 6px;
    font-size: 10px;
    color: #767676;
  }
`;

const MyChatContainerStyle = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: end;
  margin-top: 9px;
  margin-right: 15px;

  p {
    display: inline-block;
    padding: 10px;
    background-color: var(--basic-color-2);
    border-radius: 10px 0 10px 10px;
    color: #fff;
    font-size: var(--font--size-md);
    color: var(--common-text-color-1);
  }

  img {
    border-radius: 10px;
  }

  .time {
    display: flex;
    align-items: flex-end;
    margin: 6px;
    font-size: 10px;
    color: #767676;
  }
`;

const SendStyle = styled.div`
  width: var(--basic-width);
  height: 61px;
  display: flex;
  align-items: center;
  background-color: var(--background-color);

  .upload {
    margin-left: 16px;
  }

  input {
    width: 230px;
    margin: 0 18px;
    font-size: var(--font--size-md);
    background-color: var(--background-color);
  }

  button {
    width: 55px;
    height: 19px;
    font-size: var(--font--size-md);
    color: #c4c4c4;
    display: inline-block;
    transition: 0.5s;
    &:hover {
      color: var(--basic-color-5);
    }
  }
`;

const InputStyle = styled.input`
  color: var(--text-color-1);
  ::placeholder {
    color: var(--text-color-2);
  }
`;
