import { useState , useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import styled from 'styled-components';

import { getSearch } from '../api/searchAPI';
import  SearchHeader  from '../components/header/SearchHeader'
import SearchProfile from '../components/common/SearchProfile';
import { NavBar } from '../components/common/NavBar';


export default function SearchPage() {
  //  
  const [search, setSearch] = useState('')
  //    
  const [searchList, setSearchList] = useState([]) 
  //   
  const [isLoading, setIsLoading] = useState(false)

  //      search 
  const navigate = useNavigate();
  
  // GetSearch await
  //   api   
    const setResult = async () => {
      setSearchList(await getSearch(search));
    }
  //   useEffect      
  useEffect(() => {
    let timer;
    if(search.length >= 1) {      
      setResult()
      setIsLoading(true)
    } else if (search.length === 0 ){
      setIsLoading(false)
      timer = setTimeout(() => {
        setSearchList([])
      }, 500);
    }
    setIsLoading(false)
    return () => {
      clearTimeout(timer);
    }
  }, [search])

  return (
    <>
      <SearchHeader value={search} setValue={setSearch}></SearchHeader>
      <SearchPageStyle>
      <SearchResultWrapper>
        {isLoading ? null: (
          <>
          {searchList ? (
        <>
        {searchList.map((item, index) => {
        return (<li key={index}>
          <SearchProfile info={item}/>
        </li>)
      })}</>) : (
        <>
        <div>
          <h2> !</h2>
        </div>
        </>)}
        </>)}
      
      </SearchResultWrapper>
      </SearchPageStyle>
    </>
  );
}

const SearchPageStyle = styled.div`
  padding: 20px 16px 0;
  width: 390px;
  height: var(--screen-nav-height);
  margin: 0 auto;
  background-color: var(--backgroud-color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  overflow-y: scroll;
  overflow-x: hidden;
  ::-webkit-scrollbar {
    background-color: var(--background-color);
    width: 0px;
  }
`;

const SearchResultWrapper = styled.ul `
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  gap: 15px;
`